/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import productSummaryData from './data-bb-product-summary-http';

const productSummaryDataModuleKey = 'data-bb-product-summary-http-ng';

export const productSummaryDataKey = 'data-bb-product-summary-http-ng:productSummaryData';

export default ng
  .module(productSummaryDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(productSummaryDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       productSummaryData(config),
      ],
    };
  }])
  .name;



